import React from 'react'

import {
  JobHistory,
  ProjectShowcase,
  Navbar,
  Container,
  Seo
} from '../components'
import ContactForm from '../components/ContactForm'
import CallToAction from '../components/Calltoaction'
import Services from '../components/Services'
import FloatingScrollButton from '../components/FloatingScrollButton'
import { graphql } from 'gatsby'

const navbarItems = [
  { label: 'Home', name: 'home' },
  { label: 'Portfolio', name: 'portfolio' },
  { label: 'Services', name: 'services' },
  { label: 'Working experience', name: 'job-history' },
  { label: 'Contact me', name: 'contact-me' }
] 

function Home () {
  //refs to our sections we pass to navbar
  const callToActionRef = React.useRef(null)
  const projectShowcaseRef = React.useRef(null)
  const servicesRef = React.useRef(null)
  const jobHistoryRef = React.useRef(null)
  const contactFormRef = React.useRef(null)
  console.log('projectShowcaseRef', projectShowcaseRef)
  return (
    <main>
      <Seo />
      <Navbar
        sectionRefs={[
          { ref: callToActionRef, bg: 'light' },
          { ref: projectShowcaseRef, bg: 'dark' },
          { ref: servicesRef, bg: 'light' },
          { ref: jobHistoryRef, bg: 'dark' },
          { ref: contactFormRef, bg: 'light' }
        ]}
        isSticky={true}
        navbarItems={navbarItems}
      />
      <Container bgColor='light'>
        <CallToAction sectionRef={callToActionRef} />
      </Container>
      <Container bgColor='dark'>
        <ProjectShowcase sectionRef={projectShowcaseRef} />
      </Container>
      <Container bgColor='light'>
        <Services sectionRef={servicesRef} />
      </Container>
      <Container bgColor='dark'>
        <JobHistory sectionRef={jobHistoryRef} />
      </Container>
      <Container bgColor='light'>
        <ContactForm sectionRef={contactFormRef} />
      </Container>
      <FloatingScrollButton />
    </main>
  )
}
export const pageQuery = graphql`
  query {
    file(relativePath: { eq: "cat_1.jpg" }) {
      id
      childImageSharp {
        fluid {
          sizes
          src
          srcSet
        }
        gatsbyImageData
      }
    }
  }
`
export default Home
